<template>
    <v-card>
            <v-card-text class="d-flex">
                <img class="beer-image" :src="beer.image?beer.image:require('@/assets/beer.jpg')"/>
                <div class="px-3">
                    <v-card-title class="pa-0">
                        {{beer.name}}
                    </v-card-title>
                    <p>Rodzaj:
                    <v-chip v-for="type in beer.beerType" :key="type._id">
                        {{type.name}}
                    </v-chip>
                    </p>
                    <p>Zawartość alkoholu: {{beer.alcoholPercentage}}%</p>

                    <p>Browar: <router-link v-if="beer.brewery" :to="`/breweries/${beer.brewery._id}`">
                        {{beer.brewery.name}}</router-link>
                        <span v-else>nieznany</span>
                    </p>
                </div>
                <v-spacer/>
                <div>
                    srednia ocen
                    <p v-if="$store.state.isLoggedIn">
                        <v-btn icon color="danger" @click="$emit('deleteBeer')">
                            <v-icon>
                                fas fa-trash
                            </v-icon>
                        </v-btn>
                        <v-btn icon color="info" @click="$emit('editBeer')">
                            <v-icon>
                                fas fa-pen
                            </v-icon>
                        </v-btn>
                    </p>
                </div>
            </v-card-text>
        <div class="px-4 pb-4">
            <h3>Opis:</h3>
            {{beer.description}}
        </div>

    </v-card>
</template>

<script>
    export default {
        name: "BeerDetails",
        props: ['beer']
    }
</script>

<style scoped>
 .beer-image{
     width:200px;
 }
</style>