<template>
  <div>
    <v-list v-if="beers.length">
      <v-list-item-group v-model="item">
      <v-list-item two-line v-for="beer in beers" :key="beer._id" :to="`/beers/${beer._id}`">
        <v-list-item-avatar>
          <img :src="(beer.image==null)?require('@/assets/beer.jpg'):beer.image" alt />
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title>{{ beer.name }}</v-list-item-title>
          <v-list-item-subtitle>Browar:
            <router-link v-if="beer.brewery" :to="`/breweries/${beer.brewery._id}`">
            {{beer.brewery.name}}</router-link>
            <span v-else>nieznany</span>
          </v-list-item-subtitle>
          <v-list-item-subtitle>Gatunek:
            <v-chip small v-for="beerType in beer.beerType" :key="beerType._id">
              {{beerType.name}}
            </v-chip></v-list-item-subtitle>
        </v-list-item-content>
        <v-list-item-action class="flex-row" v-if="$store.state.isLoggedIn">
          <v-btn icon color="danger" @click.prevent="$emit('deleteBeer',beer._id)">
            <v-icon>
              fas fa-trash
            </v-icon>
          </v-btn>
          <v-btn icon color="info" @click.prevent="$emit('editBeer',beer)">
            <v-icon>
              fas fa-pen
            </v-icon>
          </v-btn>
        </v-list-item-action>

      </v-list-item>
      </v-list-item-group>
    </v-list>
    <p v-else>Brak piw</p>
  </div>
</template>

<script>
export default {
  name: "BeerList",
  props: ["beers"],
  data(){
    return {
      item: null
    }
  }
};
</script>

<style scoped></style>
