<template>
  <div>
    <v-app-bar app color="primary" dark>
      <v-app-bar-nav-icon
        class="d-inline-block d-lg-none"
        @click="drawer = true"
      />
      <v-spacer class="d-inline-block d-lg-none" />
      <router-link tag="h1" to="/">Oceń Piwo</router-link>
      <v-spacer></v-spacer>
      <v-navigation-drawer v-model="drawer" app absolute>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Oceń Piwo</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-list dense>
         <v-list-item-group>
           <v-list-item v-for="item in routes" :key="item.path" link :to="item.path">
             <v-list-item-content>
               <v-list-item-title>{{ item.name }}</v-list-item-title>
             </v-list-item-content>
           </v-list-item>
         </v-list-item-group>
          <v-list-item-group v-if="!$store.state.isLoggedIn">
            <v-list-item  to="/login">
              <v-list-item-title>
                Logowanie
              </v-list-item-title>
            </v-list-item>
            <v-list-item to="/register">
              <v-list-item-title >
                Rejestracja
              </v-list-item-title>
            </v-list-item>
          </v-list-item-group>
          <v-list-item-group v-else>
            <v-list-item>
              <v-list-item-title>Wyloguj</v-list-item-title>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-navigation-drawer>
      <div class="d-lg-inline-block d-none">
        <v-btn
          v-for="item in routes"
          :to="item.path"
          :key="item.path"
          text
          class="mr-2 "
          >{{ item.name }}</v-btn
        >
        <template v-if="!$store.state.isLoggedIn">
          <v-btn
                  text
                  class="mr-2 "
                  to="/register"
          >rejestracja</v-btn>
          <v-btn
                  text
                  class="mr-2 "
                  to="/login"
          >logowanie</v-btn>
        </template>
        <template v-else>
          <v-btn rounded @click="$store.dispatch('logout')">
            Wyloguj
          </v-btn>
          <v-btn  fab x-small class="ml-2" color="secondary">
            <v-icon>
              fas fa-user
            </v-icon>
          </v-btn>
        </template>

      </div>
    </v-app-bar>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      drawer: false,
      routes: [
        { name: "Wyszukiwarka", path: "/search-beer" },
        {
          name: "Browary",
          path: "/breweries"
        },
      ],
    };
  }
};
</script>

