<template>
    <v-container>
        <v-row>
            <v-col class="col-lg-8 col-12" >
                <BeerModal @beerListEdited="getBeer" :hidden="true" ref="beerModal"/>
                <BeerDetails :beer="beer" @deleteBeer="deleteBeer" @editBeer="editBeer" />
            </v-col>
            <v-col>
                <opinions :opinions="[]"/>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import BeerDetails from "../components/Beer/BeerDetails";
    import BeerModal from "../components/Beer/BeerModal";
    import Opinions from "../components/Opinions";
    export default {
        name: "ProductView",
        components: {Opinions, BeerModal, BeerDetails},
        data(){
            return {
                beer: {},
                loading:false,
            }
        },
        created() {
            this.getBeer();
        },
        methods:{
            async getBeer(){
                try{
                    this.loading=true;
                    const response = await this.$http.get(`/api/beers/${this.$route.params.id}`)
                    this.beer =response.data;
                    this.loading=false;
                }
                catch(e){
                    console.error(e)
                }
            },
            async deleteBeer(){
                try{
                    await this.$http.delete(`/api/beers/${this.beer._id}`);
                    this.$router.push('/search')
                }
                catch(e){
                    console.log(e);
                }
            },
            editBeer(){
                this.$refs.beerModal.open(this.beer);
            }
        }

    }
</script>

