<template>
  <v-card>
    <v-card-title>
      Znajdź browar:
      <BreweryModal @breweriesListEdited="searchBrewery" ref="breweryModal"/>
    </v-card-title>
    <v-card-text>
      <v-form @submit.prevent="searchBrewery">
        <v-text-field
          clearable
          v-model="searchByName"
          append-outer-icon="fas fa-search"
          @click:click:append-outer="searchBrewery"
        >
        </v-text-field>
      </v-form>
      <v-divider></v-divider>
      <v-list v-if="searchResult.length">
        <v-list-item
          two-line
          v-for="brewery in searchResult"
          :key="brewery._id"
        >
          <v-list-item-avatar>
            <img
              :src="
                brewery.image == null ? require('@/assets/brewery.jpg' ): brewery.image
              "
              alt
            />
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>{{ brewery.name }}</v-list-item-title>
            <v-list-item-subtitle
              >Adres:
              {{
                brewery.address ? brewery.address : "Nieznany"
              }}</v-list-item-subtitle
            >
          </v-list-item-content>
          <v-list-item-action class="flex-row" v-if="$store.state.isLoggedIn">
            <v-btn icon color="danger" @click="deleteBrewery(brewery._id)">
              <v-icon>
                fas fa-trash
              </v-icon>
            </v-btn>
            <v-btn icon color="info" @click="editBrewery(brewery)">
              <v-icon>
                fas fa-pen
              </v-icon>
            </v-btn>
          </v-list-item-action>
        </v-list-item>
      </v-list>
      <p v-else>Brak browarów</p>
    </v-card-text>
  </v-card>
</template>

<script>
import BreweryModal from "./BreweryModal";
export default {
	name: "SearchBrewery",
  components: {BreweryModal},
  data() {
    return {
      searchResult: [],
      searchByName: "",
      loading: false
    };
  },
  created() {
	  this.searchBrewery();
  },
  methods: {
    async searchBrewery() {
      // eslint-disable-next-line no-unused-vars,no-undef
      this.loading = true;
      const response = await this.$http.get(
        `api/breweries/?name=${this.searchByName}`
      );
      this.searchResult = response.data;
      this.loading = false;
    },
    async deleteBrewery(id){
      try{
        await this.$http.delete(`api/breweries/${id}`);
        this.searchBrewery();
      }
      catch(e){
        console.log(e);
      }
    },
    editBrewery(brewery){
      this.$refs.breweryModal.open(brewery);
    }
  }
};
</script>
