<template>
  <v-card>
    <v-card-title>
      Znajdź piwo:
      <BeerModal ref="beerModal" @beerListEdited="searchBeer"/>
    </v-card-title>
    <v-card-text>
      <v-form @submit.prevent="searchBeer">
        <v-text-field  clearable v-model="searchByName"  append-outer-icon="fas fa-search" @click:click:append-outer="searchBeer">
        </v-text-field>
      </v-form>
      <v-divider></v-divider>
      <BeerList :beers="searchResult" @deleteBeer="deleteBeer" @editBeer="editBeer" />
    </v-card-text>
  </v-card>
</template>

<script>
import BeerList from "./BeerList";
import BeerModal from "./BeerModal";
export default {
  name: "SearchBeer",
  components: {BeerModal, BeerList },
  data() {
    return {
      searchResult: [],
      searchByName: "",
      loading: false,
    };
  },
  created() {
    this.searchBeer();
  },
  methods: {
    async searchBeer() {
      // eslint-disable-next-line no-unused-vars,no-undef
      this.loading=true;
      const response = await this.$http.get(`api/beers/?name=${this.searchByName}`);
      this.searchResult = response.data;
      this.loading=false;
    },
    async deleteBeer(id){
      try{
        await this.$http.delete(`api/beers/${id}`);
        this.searchBeer();
      }
      catch(e){
        console.log(e);
      }
    },
     editBeer(beer){
      this.$refs.beerModal.open(beer);
    }
  }
};
</script>

<style scoped></style>
